<template>
  <div class="main">
    <div class="outer">
      <!-- ha插件版本不需要手动连接 -->
      <el-row class="row-setting">
        <el-tooltip
          effect="dark"
          content="Home Assistant Setting"
          placement="left"
        >
          <el-badge is-dot :hidden="!needHaSetting">
            <i class="el-icon-setting" @click="haSettingDialogVisible = true" />
          </el-badge>
        </el-tooltip>
      </el-row>
      <div class="inner">
        <div class="title">
          <p>Welcome to KoolSmart</p>
        </div>
        <!-- <TestHA /> -->
        <div class="form">
          <el-form>
            <el-form-item>
              <p class="name">Account Type</p>
              <el-select v-model="loginForm.accountType" placeholder="请选择">
                <el-option
                  v-for="item in accountTypeOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                  :disabled="item.disable"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <p class="name">Email or User name</p>
              <el-input v-model="loginForm.email"></el-input>
            </el-form-item>
            <el-form-item>
              <p class="name password">Password</p>
              <el-input type="password" v-model="loginForm.password"></el-input>
            </el-form-item>
            <el-form-item class="name">
              <el-checkbox
                v-model="isRememberPassword"
                @change="handleIsRememberPasswordChange"
                >Remember me</el-checkbox
              >
            </el-form-item>
          </el-form>
        </div>
        <div class="footer">
          <el-button
            class="primary-btn"
            round
            type="primary"
            @click="login"
            :loading="isLogining"
            >{{ isLogining ? "Logging in" : "Log in" }}</el-button
          >
          <el-button round @click="guest" disabled>Guest</el-button>
        </div>
      </div>
    </div>
    <el-dialog
      title="Home Assistant Setting"
      :visible.sync="haSettingDialogVisible"
      :modal-append-to-body="false"
      width="600px"
      class="basic-dialog"
    >
      <el-form
        :model="haSettingForm"
        label-width="200px"
        label-position="left"
        width="400px"
      >
        <el-form-item label="Home Assistant Url">
          <el-input v-model="haSettingForm.hassUrl">
            <i slot="prefix" class="el-input__icon el-icon-position"></i>
          </el-input>
        </el-form-item>
        <el-form-item label="Long-Lived Access Token">
          <el-input v-model="haSettingForm.accessToken">
            <i slot="prefix" class="el-input__icon el-icon-key"></i>
          </el-input>
        </el-form-item>
      </el-form>
      <el-button
        class="basic-btn"
        @click="handleHAConnectBtnClick"
        :loading="isHAConnecting"
        >Connect</el-button
      >
    </el-dialog>
  </div>
</template>

<script>
import throttle from "@/utils/throttle";
import { behaviorLog } from "@/utils/behavior";
import { login } from "@/services";
import Cookies from "js-cookie";
export default {
  data() {
    return {
      VUE_APP_MODE: process.env.VUE_APP_MODE,
      haSettingForm: {
        hassUrl: "",
        accessToken: "",
      },
      loginForm: {
        accountType: "",
        email: "",
        password: "",
      },
      accountTypeOptions: [
        { value: "coohom", label: "coohom", disable: true },
        { value: "kujiale", label: "酷家乐", disable: false },
      ],
      needHaSetting: false,
      haSettingDialogVisible: false,
      isRememberPassword: false,
      isLogining: false,
      isHAConnecting: false,
    };
  },
  async created() {
    let haSettingLocal = this.getHaSettingFromLocal();
    this.haSettingForm = haSettingLocal;
    if (!haSettingLocal.hassUrl || !haSettingLocal.accessToken) {
      this.needHaSetting = true;
    }
    // 加载本地账号配置
    const accountType = window.localStorage.getItem("accountType");
    const email = window.localStorage.getItem("email");
    const isRememberPasswordLocal =
      window.localStorage.getItem("isRememberPassword");
    if (accountType) {
      this.loginForm.accountType = accountType;
    }

    if (email) {
      this.loginForm.email = email;
    }

    if (isRememberPasswordLocal !== "0") {
      this.isRememberPassword = true;
      const password = window.localStorage.getItem("password");
      this.loginForm.password = password;
    }
  },
  methods: {
    handleHAConnectBtnClick: throttle(async function () {
      this.isHAConnecting = true;
      if (!this.haSettingForm.hassUrl || !this.haSettingForm.accessToken) {
        this.$message.error({
          title: "error",
          message: "Please complete Home Assistant setting first.",
        });
        return;
      }
      let connectRes = await this.$store.dispatch("haStore/createHAWebsocket", {
        hassUrl: this.haSettingForm.hassUrl,
        accessToken: this.haSettingForm.accessToken,
        vm: this,
      });
      if (connectRes) {
        this.$notify.success({
          title: "Success",
          message: "Home Assistant Connect Success",
        });
        window.localStorage.setItem(
          "hassTokens",
          JSON.stringify({
            hassUrl: this.haSettingForm.hassUrl,
            access_token: this.haSettingForm.accessToken,
          })
        );
        window.sessionStorage.setItem("hassUrl", this.haSettingForm.hassUrl);
        window.sessionStorage.setItem(
          "accessToken",
          this.haSettingForm.accessToken
        );
        this.needHaSetting = false;
        this.haSettingDialogVisible = false;
      }
      this.isHAConnecting = false;
    }),

    getHaSettingFromLocal() {
      let haSettingLocal = { hassUrl: "", accessToken: "" };
      let hassTokens = JSON.parse(window.localStorage.getItem("hassTokens"));
      if (hassTokens) {
        haSettingLocal.hassUrl = hassTokens["hassUrl"] || "";
        haSettingLocal.accessToken = hassTokens["access_token"] || "";
      }
      return haSettingLocal;
    },
    handleIsRememberPasswordChange(value) {
      window.localStorage.setItem("isRememberPassword", value ? "1" : "0");
    },
    eventTrack(status) {
      behaviorLog("koolsmart_user_active", { ActiveStatus: status });
    },
    validateHAConnect() {
      // If the WebSocket connection fail, you need to refresh the page.
      if (!this.$store.state.haStore.connectSuccess) {
        this.$notify.error({
          title: "error",
          message: "Please complete Home Assistant setting first.",
        });
        return false;
      }
      return true;
    },
    login: throttle(async function () {
      if (!this.validateHAConnect()) return;
      // login 加载态开启
      this.isLogining = true;
      let loginData = {
        email: this.loginForm.email,
        password: this.loginForm.password,
        grantType: "password",
      };
      let res;
      res = await login(loginData, this.loginForm.accountType).then(
        (res) => {
          if (res.c !== "0" || res.c === "BAD_CREDENTIAL") {
            this.$message.error(res.m);
            this.isLogining = false;
            return null;
          }
          return res;
        },
        (err) => {
          this.$message.error(err);
          // console.log("Login failed", err); // for debug
          this.isLogining = false;
          return null;
        }
      );
      if (res) {
        window.sessionStorage.setItem("ksToken", res.d);
        Cookies.set("qunhe-jwt", res.d);
        this.$message.success("Login successful");
        this.$store.commit(
          "koolsmartStore/setAccountType",
          this.loginForm.accountType
        );
        // Event Tracking
        this.eventTrack("Logged");

        this.saveLoginFormLocally(
          {
            email: this.loginForm.email,
            password: this.loginForm.password,
            accountType: this.loginForm.accountType,
          },
          this.isRememberPassword
        );
        this.$router.replace("/project");
      }
      this.isLogining = false;
    }),
    guest() {
      if (!this.validateHAConnect()) return;
      this.eventTrack("Visitor");
      // Mark as guest mode
      this.$store.commit("setIsVisitor", true);
      this.$router.push("/project");
    },
    saveLoginFormLocally({ accountType, email, password }, isRememberPassword) {
      window.localStorage.setItem("accountType", accountType);
      window.localStorage.setItem("email", email);
      if (isRememberPassword) {
        window.localStorage.setItem("password", password);
      }
    },
  },
};
</script>

<style lang="less" scoped>
@import "./index.less";

.basic-dialog ::v-deep {
  .el-dialog {
    border-radius: 10px;
    padding: 10px 20px 12px;
    text-align: center;
    max-height: 600px;
    overflow: auto;
  }
}
</style>
